/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1740px)) {
    @content;
  }
}

@mixin between {
  @media (max-width: (1400px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (1000px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (500px)) {
    @content;
  }
}
